* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.handle_cards {
    width: calc(95% / 8);
    display: flex;
    justify-content: center;
    margin: 1px;
    height: 265px;
    border: 3px solid #fff;
    overflow: hidden;
  
}

.cards .cards_img {
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;

}

.cards_overlay {
    color: #fff;
    transform: translateY(-120%);
    height: 30vh;
    opacity: 0;
    transition: 1s;
    font-size: 0.9em; padding: 5px;


}

.handle_cards:hover .cards_overlay {
    opacity: 1;
}

.handle_cards:hover .cards_img {
    transform: scale(1.02);
    opacity: 0.5;
}
.play{
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    color: red;
    width: 10px;
    height: 5vh;
}