.list_cards {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    
}
.Title{
    text-align: center;
    margin: 50px;
    font-size: 1.5rem;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
}
@media screen and (max-width:400px){
    .list_cards{
        display: block;
        flex-wrap: wrap;
        width: 100%;
    }
    .handle_cards{
        width: 100%;
       
    }
}