.header{
    width: 100%;
    background: #000;
    
}
.header .header_icon{
    width: 10%;
    display: flex;
    align-items: center;
}
.header .header_icon img{
    width: 100%;
}
.header .headerLeft{
    width: 100%;
    display: flex;
    align-items: center;
}

.header span{
    width: 100%;
    margin: 10px;
    color: #fff;
    font-size: 1.2rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
}
